import InfoTable from "../../plugin/components/InfoTable";
import PageInfo from "../../plugin/script/common/PageInfo";
import InfoTableItem from "../../plugin/components/InfoTableItem";
import InputArea from "../../plugin/components/InputArea";
export default {
  name: "PayOrderRecord",
  components: {
    InfoTableItem,
    InfoTable,
    InputArea
  },
  data() {
    return {
      pagesize: 5,
      currentPage: 1,
      total: 0,
      tableData: [],
      dialogFormVisible: false,
      dialogVisible: false,
      formLabelWidth: "160px",
      dialogTitle: "",
      tableStyle: [
        { label: "ID", prop: "id", width: "" },
        { label: "兑换方式", prop: "method", width: "" },
        { label: "当前状态", prop: "online_status", width: "" },
        { label: "兑换最小额度(元)", prop: "min_money", width: "" },
        { label: "兑换最大额度(元)", prop: "max_money", width: "" },
        { label: "角标", prop: "thumb", width: "" },
        { label: "操作", prop: "action", width: "200" }
      ],
      records: [],
      pageInfo: new PageInfo(1, [5, 10, 15], 0),
      form: {
        id: null,
        pay_type: "",
        exchange_min: "",
        exchange_max: "",
        status: ""
      },
      form2: {
        keep_money: 1000.0
      },
      fileList: []
    };
  },
  filters: {
    formatStatus: function(val) {
      return val === 1 ? "下线" : "上线";
    }
  },
  methods: {
    resetForm() {
      this.form = {
        id: null,
        pay_type: "",
        exchange_min: "",
        exchange_max: "",
        status: ""
      };
    },
    async getExchangeList() {
      const res = await this.$http.get("api/lobby/conversion",{
        params:  {
          page: this.currentPage,
          limit: this.pagesize
        }
      });
      console.log(res);
      if (res.data.code === 1) {
        this.records = res.data.data;
        this.total = res.data.total
      }
    },
    async addConfig() {
      if (!this.form.id) {
        let data = {
          method: Number(this.form.pay_type),
          keep_money: this.form2.keep_money,
          min_money: Number(this.form.exchange_min),
          max_money: Number(this.form.exchange_max),
          online_status: Number(this.form.status),
          thumb: "thumb"
        };
        console.log(data);
        const res = await this.$http.post("api/lobby/conversion", data);
        if (res.data.code === 1) {
          this.dialogFormVisible = false;
          this.getExchangeList();
        }
        console.log(res);
      } else {
        let data = {
          method: Number(this.form.pay_type),
          keep_money: this.form2.keep_money,
          min_money: Number(this.form.exchange_min),
          max_money: Number(this.form.exchange_max),
          online_status: Number(this.form.status),
          thumb: "thumb",
          id: this.form.id
        };
        const res = await this.$http({
          method: "put",
          url: "api/lobby/conversion",
          data: data
        });
        console.log(res);
        if (res.data.code === 1) {
          this.dialogFormVisible = false;
          this.getExchangeList();
        }
      }
    },
    openAdd() {
      this.dialogFormVisible = true;
      this.dialogTitle = "添加配置信息";
      this.resetForm();
    },
    handleEdit(row) {
      console.log(row);
      this.dialogFormVisible = true;
      this.dialogTitle = "编辑配置信息";
      this.form.id = row.id;
      this.form.pay_type = String(row.method);
      this.form.exchange_min = row.min_money;
      this.form.exchange_max = row.max_money;
      this.form.status = String(row.status);
    },
    handleDelete(row) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$http
            .delete("api/lobby/conversion", {
              params: {
                id: row.id
              }
            })
            .then(res => {
              if (res.data.code === 1) {
                this.getExchangeList();
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    updateStatus(row) {
      this.$confirm("确认更新状态吗?", "信息", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        let data = {
          id: row.id,
          online_status: row.online_status === 1 ? 2 : 1
        };
        this.$http.patch("api/lobby/conversion", data).then(res => {
          if (res.data.code === 1) {
            this.getExchangeList();
          }
        });
      });
    },
    setKeepMoney() {
      this.dialogVisible = false;
      this.$message({
        type: "success",
        message: "设置成功"
      });
    },
    httpRequest(params) {
      const file = params.file,
        fileType = file.type,
        isImage = fileType.indexOf("image") != -1,
        isLt2M = file.size / 1024 / 1024 < 2;
      // 这里常规检验，看项目需求而定
      if (!isImage) {
        this.$message.error("只能上传图片格式png、jpg、gif!");
        return;
      }
      if (!isLt2M) {
        this.$message.error("只能上传图片大小小于2M");
        return;
      }
    },
    handleSizeChange(val) {
      this.pagesize = val;
      this.getExchangeList();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getExchangeList();
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    open() {
      this.$prompt("保留金额", "设置保留金额", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        closeOnClickModal: true,
        inputValue: this.keep_money,
        inputType: "number"
      })
        .then(({ value }) => {
          this.$message({
            type: "success",
            message: "操作成功"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "取消输入"
          });
        });
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    }
  },
  mounted() {
    this.getExchangeList();
  }
};



